import cdnConfig from '~global/variables/cdn-config.json';
import getEnvironment, { environments } from '~global/js/helpers/environment';

const cdnPaths = {
  [environments.DEV]: cdnConfig.urls.dev,
  [environments.PROD]: cdnConfig.urls.prod,
  [environments.STAGE]: cdnConfig.urls.stage,
  [environments.UAT1]: cdnConfig.urls.uat,
  [environments.UAT2]: cdnConfig.urls.uat,
  [environments.UAT3]: cdnConfig.urls.uat,
};

export default function getCdnBasePath() {
  return cdnPaths[getEnvironment()];
}



